@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Lobster&family=Roboto&display=swap');

$bg-primary-color: #100e17;
$bg-secondary-color: #1F89DD;
$bg-alternative-color: #2438e79c;
$font-color: #d5dfde;

$font-primary: Dosis, Roboto, sans-serif;
$font-secondary: Roboto, sans-serif;
$font-alternative: Lobster, Dosis, sans-serif;

// ==> General <==
*{
  margin: 0;
  padding: 0;
}

body {
  color: $font-color;
  background-color: $bg-primary-color;
  font-family: $font-secondary;
  outline: none;
}

.container{
  margin: 0em 2em;
  // background-color: #8ea1e6; //Temporario
}

li {
  list-style: none;
}


a {
  text-decoration: none;
  color: $font-color;

  
}


// ==> Inicial <=> Navigation <==
.ld-header {
  position: fixed;
  width: 100%;
  height: 12.5vh;
  z-index: 5;
  transition: 0.3s ease-in-out;

  .container {
    padding: 0em 1.5em;
  }
  
  nav {
    // background-color: #1F89DD; //Temporario
    width: 100%;
    height: 12.5vh;
    display: flex;
    
    .navbar-brand {
      // background-color: #d85c5c; //Temporario
      width: 15%;
      display: flex;
      padding-left: 5rem;
      align-self: center;
  
      #brand{
        font-family: $font-alternative;
        font-size: 40px;
        transition: 0.2s ease-in-out;
  
        &:hover{
          color: $bg-secondary-color;
        }
  
        b{
          font-family: $font-primary;
        }
      }
    }
    
    .navbar-collapse{
      width: 75%;
      align-self: center;
      
      ul{
        height: 50px;
        display: flex;
        flex-direction: row;
        justify-content: center;   
        // background-color: #27406c; //Temporario
        padding: 0px 0px 0px 80px;
      }
      
      li{
        // background-color: #24a06c; //Temporario
        align-self: center;
        padding: 15px 25px;
      }
      
      a {
        font-family: $font-primary;
        font-size: 19px;
        padding: 7px 1px;
        transition: 0.3s ease-in-out;
        
        &:hover{
          border-bottom: 1px #d5dfde solid;
          color: $bg-secondary-color;
        }
      }      
    }
  }
}
// ==> End <=> Navigation <==



// ==> Begin <=> Home <==
.ld-home {
  position: relative;
  width: 100%;
  height: 112.4vh;
  background-image: url("../img/background.jpg");
  background-attachment: fixed;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;  
  ;
  
  .header-bg {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 3;
    background-color: #000a;
  }

  .col-header  {
    // background-color: sienna; // Temporario
    position: relative;
    top: 6rem;
    display: flex;
    flex-direction: row;
    
    .header-img{ 
      // background-color: rgb(145, 167, 156); //Temporario
      width: 50%;
      height: 100vh;
      
      .border-img {
        position: relative;
        margin-left: 15px;

        // background-color: springgreen; //Temporario

        & img{
          width: 33.5vw;
          
          margin-left: 60px;
          // background-color: bisque; //Temporario
        }
      }
    }
    // .header-img{      
      
    //   .border-img {
    //     position: relative;
        
    //     & img{
    //       position: absolute;
    //       left: 6rem;
    //       height: 41rem;
    //       filter: grayscale(0.1) contrast(0.895) brightness(0.9);
    //     }
    //   }
    // }
    
    
    .header-info{
      // background-color: slateblue; //Temporario
      position: relative;
      padding: 80px 25px 25px;
      font-family: $font-primary;
      font-size: 18px;
      width: 50%;
      
      
      
      .ini-info {
        margin-left: 0.8rem;
        
        span {
          background-color: $bg-secondary-color;
          // width: 18em;
          border-radius: 27px 27px 27px 0px;
          padding: 12px 25px;
          text-align: center;
          font-size: 20px;
        }
      }
      
      .nm-info{
        // background-color: yellow;
        padding: 12px;
        margin-top: 25px;
        font-family: $font-primary;
        
        h2{
          font-size: 50px;
        }
        
        h4 {
          font-size: 28px;
        }
      }
      
      .link-info{
        padding: 12px;
        display: flex;
        flex-direction: column;
        
        .lt-info{
          padding: 5px 10px;
          display: inline-flex;
          transition: 0.2s ease-in-out;
          
          &:hover{
            color: $bg-secondary-color;
            font-style: italic;
          }
          
          i{
            // background-color: tan;
            width: 10px;
            display: flex;
            justify-content: center;
            margin-right: 20px;
            
          }
        }
          
      }
      .social-link{
        // background-color: violet;
        margin: 3px;
        display: flex;

        .social-ln {
          margin-left: 5px;
          width: 40px;
          height: 40px;
          
          border-radius: 25px;
          display: flex;
          justify-content: center;
          
          a, i{
            width: 40px;
            height: 40px;
            border-radius: 25px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px #d5dfde solid;
            transition: 0.2s ease-in-out;
            
            &:hover > .fa-facebook{
              background-color: #3B5999;
            }
            
            &:hover > .fa-instagram{
              background-color: #044B7F;
            }
            
            &:hover > .fa-linkedin{
              background-color: #0077B5;
            }

            &:hover > .fa-github{
              background-color: #000;
            }
          }

        }
      }
    }
  }
}

// ==> End <=> Home <==

// ==> Begin <=> About <==
.ld-about {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: $bg-primary-color;

  .container{
    margin: 50px 50px;
    display: flex;
    

    .col-section{
      flex-direction: row;
      width: 50%;
      
      
      .ld-about-img {
        height: 30em;
        margin-left: 15px;
        padding: 5em;
        // background-color: red; //Temporario
        
        & img{
          width: 100%;
        }
      }
      
      .about-inner{
        width: auto;
        height: 20em;
        margin-top: 5em;
        padding: 6em;
        // background-color: red; //Temporario
        
        h2 {
          font-family: $font-primary;
          font-size: 40px;
        }
        
        p {
          font-family: $font-secondary;
          text-align: justify;
          margin: 20px 0;
        }
        
        ul {
          display: flex;
          justify-content: center;
          justify-items: self-end;
          padding: 10px 5px;
          
          li {
            list-style: none;
            border: 1px solid $bg-secondary-color;
            border-radius: 7px;
            margin: 8px 3px;
            font-size: 15px;
            padding: 4px 10px;
            transition: ease-in-out .2s;
            margin-bottom: 3em;
            
            &:hover {
              background-color: $bg-secondary-color;
            }
          }
        }
        
        a {
          border: 1px solid $bg-secondary-color;
          border-radius: 2em;
          padding: 5px 25px;
          background-color: $bg-secondary-color;
          transition: ease-in-out .2s;
          
          &:hover {
            background-color: $bg-primary-color;
          }
        }
      }
    }
  }
}
// ==> End <=> About <==

